$maxWidthMB: 768px;
                   @import "assets/main/scss/mixin.scss";
              
@import "./scss/sprite.scss";

#footer {
  position: relative;
  height: 260px;
  background-color: #f7c30f;
  .background {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .content {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    box-sizing: border-box;
  }
  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;
    .list-icons {
      gap: 24px;
      display: flex;
      margin-top: 18px;
      a {
        img {
          background-color: white;
          border-radius: 50%;
          border: 4px solid white;
          transition: all 0.2s;
        }
        &:hover img {
          border: 4px solid red;
        }
      }
    }
  }
}
