
                $minWidthPC: 769px;
$maxWidthMB: 768px;
                   @import "assets/main/scss/mixin.scss";
              
@import "./scss/sprite.scss";

#contact {
  position: relative;
  height: 1000px;
  margin-top: -2px;
  @include mobile {
    height: 1300px;
  }
  .background {
    position: absolute;
    top: 0;
    left: 0;
    background-color: #2e2e2e;
    width: 100%;
    height: 100%;
  }
  .content {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    box-sizing: border-box;
  }
  .content {
    padding: 0 7%;
    .line {
      position: absolute;
      bottom: 180px;
      left: 0;
      @include mobile {
        bottom: 620px;
        left: -58px;
      }
    }
    .title {
      display: flex;
      flex-direction: column;
      position: absolute;
      top: 200px;
      width: 60%;
      @include mobile {
        width: 85%;
        text-align: center;
        top: 150px;
      }
    }
    .list-btns {
      display: flex;
      flex-wrap: wrap;
      position: absolute;
      top: 420px;
      left: 220px;
      gap: 34px;
      max-width: 800px;
      @include mobile {
        gap: 28px;
        width: 85%;
        left: auto;
        justify-content: center;
        top: 390px;
      }
      .btn {
        border: 2px solid #f7c30f;
        font-size: 34px;
        color: white;
        width: 380px;
        height: 100px;
        cursor: pointer;
        border-radius: 8px;
        transition: all 0.2s;
        @include mobile {
          width: 200px;
          height: 75px;
          font-size: 24px;
        }
        &:hover,
        &.active {
          background-color: #f7c30f;
          color: white;
        }
        &:hover {
          @include pc {
            color: red;
          }
        }
      }
    }
    #myForm {
      position: absolute;
      left: 1290px;
      top: 220px;
      display: flex;
      flex-direction: column;
      gap: 28px;
      @include mobile {
        left: auto;
        top: 640px;
        width: 85%;
        align-items: center;
      }
      input,
      textarea {
        width: 560px;
        height: 72px;
        padding-left: 18px;
        box-sizing: border-box;
        font-size: 24px;
        border-radius: 8px;
        background-color: transparent;
        color: white;
        border: 2px solid #f7c30f;
        padding-top: 9px;
        @include mobile {
          width: 420px;
        }
        &::placeholder {
          font-size: 28px;
        }
      }
      textarea {
        padding-top: 12px;
        height: 234px;
        background-color: #2e2e2e;
      }

      $main-color: #f7c30f;
      $shadow-color: rgba(0, 0, 0, 0.5);
      $hover-shadow-color: #f7c30f;
      $glow-color: rgba(255, 255, 255, 0.05);
      $grad-start: #8f6a00;
      $grad-end: #f7c30f;

      .button {
        position: relative;
        padding: 30px 60px;
        color: $main-color;
        text-transform: uppercase;
        text-decoration: none;
        letter-spacing: 5px;
        font-size: 30px;
        box-shadow: 0 5px 10px $shadow-color;
        overflow: hidden;
        background: transparent;
        border: none;
        cursor: pointer;
        transition: box-shadow 0.3s ease;
        @include mobile {
          padding: 15px 45px;
          margin-top: 12px;
        }
        &:hover {
          @include pc {
            box-shadow: 0 5px 10px $hover-shadow-color;
          }
        }

        &::before {
          content: "";
          position: absolute;
          top: 2px;
          left: 2px;
          bottom: 2px;
          width: 50%;
          background: $glow-color;
        }

        span {
          &:nth-child(2) {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 2px;
            background: linear-gradient(to right, $grad-start, $grad-end);
            animation: animate1 2s linear infinite;
            animation-delay: 1s;
          }

          &:nth-child(3) {
            position: absolute;
            top: 0;
            right: 0;
            width: 2px;
            height: 100%;
            background: linear-gradient(to bottom, $grad-start, $grad-end);
            animation: animate2 2s linear infinite;
            animation-delay: 2s;
          }

          &:nth-child(4) {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 2px;
            background: linear-gradient(to left, $grad-start, $grad-end);
            animation: animate3 2s linear infinite;
            animation-delay: 1s;
          }

          &:nth-child(5) {
            position: absolute;
            top: 0;
            left: 0;
            width: 2px;
            height: 100%;
            background: linear-gradient(to top, $grad-start, $grad-end);
            animation: animate4 2s linear infinite;
            animation-delay: 2s;
          }
        }
      }

      // Animations
      @keyframes animate1 {
        0% {
          transform: translateX(-100%);
        }
        100% {
          transform: translateX(100%);
        }
      }

      @keyframes animate2 {
        0% {
          transform: translateY(-100%);
        }
        100% {
          transform: translateY(100%);
        }
      }

      @keyframes animate3 {
        0% {
          transform: translateX(100%);
        }
        100% {
          transform: translateX(-100%);
        }
      }

      @keyframes animate4 {
        0% {
          transform: translateY(100%);
        }
        100% {
          transform: translateY(-100%);
        }
      }
    }
  }
}
