
                $minWidthPC: 769px;
$maxWidthMB: 768px;
                   @import "assets/main/scss/mixin.scss";
              
@import "./scss/sprite.scss";

@font-face {
  font-family: "Guardian Angle Straight";
  src: url("./Guardian Angle Straight.otf");
}
@font-face {
  font-family: "Gill Sans Ultra Bold";
  src: url("./Gill Sans Ultra Bold.otf");
}

// Reset css
* {
  &::-webkit-scrollbar {
    width: 6px;
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 0 !important;
  }
  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px #ccc;
    border-radius: 0 !important;
  }
  &::-webkit-scrollbar-thumb {
    background: #f7c30f;
  }
}

body {
  font-family: "Arial";
}
h1 {
  font-size: 62px;
  font-family: "Gill Sans Ultra Bold";
  @include mobile {
    font-size: 54px;
  }
}
h2 {
  font-size: 52px;
  font-family: "Gill Sans Ultra Bold";
  color: #f7c30f;
  @include mobile {
    font-size: 42px;
  }
}
p {
  font-size: 28px;
  line-height: 42px;
  color: white;
  @include mobile {
    font-size: 22px;
    line-height: 32px;
  }
}

.popup {
  display: none;
  max-width: 65%;
  @include mobile {
    min-width: 90%;
    max-width: 90%;
  }
  p {
    color: black;
  }
}


$yellow: oklch(85.33% 0.185 92.12);
$white: oklch(100% 0 0);
.back-to-top {
  border: 2px solid currentColor;
  border-radius: 0.5em;
  display: inline-block;
  padding: 0.3em;
  position: fixed;
  top: 760px;
  transform: scale(1.5);
  right: 2%;
  pointer-events: auto;
  overflow: hidden;
  color: currentColor;
  text-decoration: none;
  z-index: 999;
  @include mobile {
    display: none;
  }
  span {
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    height: 1px;
    overflow: hidden;
    position: absolute;
    white-space: nowrap;
    width: 1px;
  }

  svg {
    display: block;
    fill: none;
    stroke: currentColor;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke-width: 3px;
    transition: transform 0.2s ease;
  }
}
#profile {
  position: relative;
  height: 1000px;
  background-color: #2f2f2f;
  .background {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    canvas {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  .content {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    box-sizing: border-box;
  }
  .content {
    padding: 0 7%;
    h1 {
      color: white;
    }
    .logo {
      position: absolute;
      top: 30px;
    }
    .box.text {
      position: absolute;
      top: 20%;
      left: 7%;
      color: white;
      @include mobile {
        top: 23%;
      }
      .say-hi {
        display: flex;
        flex-direction: column;
      }
      .title {
        display: inline-block;
        @include mobile {
          color: white;
          font-family: Arial;
          font-weight: 400;
          font-style: italic;
          text-shadow: 4px 4px 4px black;
        }
      }
      .description {
        position: absolute;
        top: 230px;
        width: 690px;
      }
      .list-icons {
        gap: 24px;
        display: flex;
        position: absolute;
        top: 435px;
        @include mobile {
          top: 204px;
        }
        a {
          img {
            background-color: white;
            border-radius: 50%;
            border: 4px solid white;
            transition: all 0.2s;
          }
          &:hover img {
            border: 4px solid #f7c30f;
          }
        }
      }
    }
    .character {
      position: absolute;
      left: 47%;
      top: 8%;
      z-index: 2;
      transform-origin: top;
      @include mobile {
        left: 26%;
        top: 130px;
      }
      #spineCanvas {
        position: absolute;
        top: 0;
        left: 0;
        width: 1047px;
        height: 896px;
        z-index: 1;
        filter: drop-shadow(2px 2px 2px black);
      }
      #baseCanvas2 {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;
      }
    }

    .footer {
      @keyframes scroll-left {
        0% {
          transform: translateX(
            -100%
          ); /* Bắt đầu từ bên phải (100% chiều rộng) */
        }
        100% {
          transform: translateX(
            100%
          ); /* Chạy ra ngoài bên trái (100% chiều rộng) */
        }
      }
      @keyframes bgShift {
        0% {
          background-position: 100% 0; /* Bắt đầu từ màu cuối (f7c30f) */
          background: #f7c30f; /* Màu ban đầu */
        }
        100% {
          background-position: 0 0; /* Kết thúc tại màu 4abba9 */
          background: #4abba9; /* Màu cuối cùng */
        }
      }

      position: absolute;
      left: 0;
      bottom: 0;
      height: 167px;
      width: 100%;
      z-index: 2;
      box-sizing: border-box;
      padding: 0 7%;
      background-color: #f7c30f;
      transition: background 0.5s ease;
      @include mobile {
        border-top: 2px solid ghostwhite;
        border-bottom: 2px solid ghostwhite;
      }
      .main {
        color: white;
        display: flex;
        align-items: center;
        gap: 86px;
        font-size: 32px;
        height: 100%;
        text-transform: uppercase;
        @include mobile {
          height: 78px;
          gap: 0;
          justify-content: space-around;
        }
        .note {
          display: flex;
          gap: 18px;
          align-items: center;
          .amount {
            font-size: 104px;
            font-family: "Guardian Angle Straight";
            @include mobile {
              font-size: 62px;
            }
          }
          .text {
            font-weight: bold;
          }
        }
        .list-icons {
          display: flex;
          gap: 36px;
          margin-left: auto;
          @include mobile {
            position: absolute;
            left: 50%;
            transform: translateX(-50%) scale(0.9);
            top: 85px;
            &:before {
              content: "";
              width: 70%;
              height: 2px;
              position: absolute;
              top: -15px;
              left: 50%;
              transform: translateX(-50%);
              background-color: ghostwhite;
            }
          }
        }
      }
    }
  }
}
