@mixin image($folder, $filename, $width: auto, $height: auto) {
  background-image: url("../images/#{$folder}/#{$filename}");
  background-repeat: no-repeat;
  width: $width;
  height: $height;
}

@mixin mobile {
  @media (max-width: $maxWidthMB) {
    @content;
  }
}
@mixin pc {
  @media (min-width: $minWidthPC) {
    @content;
  }
}
