
                $minWidthPC: 769px;
$maxWidthMB: 768px;
                   @import "assets/main/scss/mixin.scss";
              
@import "./scss/sprite.scss";

#projects {
  position: relative;
  height: 1000px;
  margin-top: -2px;
  .background {
    position: absolute;
    top: 0;
    left: 0;
    background-color: #2e2e2e;
    width: 100%;
    height: 100%;
  }
  .content {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    box-sizing: border-box;
  }
  .content {
    padding: 0 7%;
    @include mobile {
      text-align: center;
    }
    .line {
      position: absolute;
      bottom: 60px;
      left: 0;
      @include mobile {
        bottom: -26px;
      }
    }
    .title {
      display: flex;
      flex-direction: column;
      position: absolute;
      top: 200px;
      width: 50%;
      @include mobile {
        top: 170px;
        width: 90%;
        position: relative;
        margin: auto;
      }
    }
    .categories {
      position: absolute;
      top: 390px;
      font-size: 28px;
      display: flex;
      gap: 18px;
      max-width: 750px;
      flex-wrap: wrap;
      @include mobile {
        max-width: 85%;
        justify-content: center;
        top: 360px;
      }
      button {
        background-color: black;
        padding: 8px 28px;
        color: white;
        border-radius: 34px;
        cursor: pointer;
        &.active {
          background-color: #f7c30f;
          color: white;
        }
      }
    }
    .mySwiper {
      position: absolute;
      left: 0;
      top: 48%;
      width: 100%;
      height: 500px;
      transform: scale(0.87);
      pointer-events: none;
      @include mobile {
        top: 515px;
        transform: scale(1);
      }
      .swiper-wrapper {
        align-items: center;
        .swiper-slide {
          height: 360px;
          display: flex;
          align-items: center;
          justify-content: center;
          display: none;
          .item {
            pointer-events: auto;
            width: 600px;
            height: 300px;
            border: 4px solid #f7c30f;
            border-radius: 12px;
            position: relative;
            transition: all 0.3s ease;
            &.front-end {
              .ribbon {
                background-color: #10b981;
              }
            }
            &.back-end {
              .ribbon {
                background-color: #4b5563 ;
              }
            }
            &.full-stack {
              .ribbon {
                background-color: #3b82f6;
              }
            }
            &.game {
              .ribbon {
                background-color: #ef4444;
              }
            }
            &:hover {
              @include pc {
                transform: scale(1.1);
                cursor: pointer;
                filter: drop-shadow(0 0 6px #f7c30f);
                .border {
                  img {
                    width: 600px;
                    height: 300px;
                    border-radius: 8px;
                  }
                }
                .info,
                .ribbon {
                  opacity: 0;
                }
              }
            }
            &:before {
              content: "";
              @include sprite($item-effect);
              position: absolute;
              top: -25px;
              left: 35px;
              transform: scale(1.5);
            }
            &:after {
              content: "";
              @include sprite($item-effect2);
              position: absolute;
              bottom: -35px;
              right: -5px;
            }
            .border {
              background-color: white;
              border-radius: 8px;
              width: 100%;
              height: 100%;
              img {
                border-radius: 4px;
                width: 580px;
                height: 280px;
                object-fit: cover;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                transition: all 0.5s;
                transition-delay: 0.1s;
              }
            }

            .info {
              position: absolute;
              bottom: 0;
              width: 100%;
              height: auto;
              padding: 12px 24px;
              box-sizing: border-box;
              display: flex;
              flex-direction: column;
              justify-content: center;
              font-size: 28px;
              color: white;
              border-bottom-left-radius: 8px;
              border-bottom-right-radius: 8px;
              border-top: 4px solid #f7c30f;
              background-color: black;
              transition: all 1s;
              p {
                line-height: 30px;
                @include mobile {
                  line-height: 32px;
                }
              }
              .name {
                font-family: "Gill Sans Ultra Bold";
                @include mobile {
                  font-size: 28px;
                }
              }
              .tag {
                font-size: 21px;
                @include mobile {
                  font-size: 24px;
                }
                span {
                  color: #f7c30f;
                  font-weight: bold;
                }
              }
            }
          }
        }
      }
      .swiper-pagination {
        bottom: 0px;
        @include mobile {
          bottom: 30px;
        }
        &-bullet {
          width: 15px;
          height: 15px;
          @include mobile {
            width: 12px;
            height: 12px;
          }
          &-active {
            background-color: #f7c30f;
          }
        }
      }
      
    }
  }
}
/* HTML: <div class="ribbon">Your text content</div> */
.ribbon {
  font-size: 20px;
  font-weight: bold;
  color: #fff;
  box-shadow: 0px -1px 6px black;
  transition: opacity 1s;
  transition-delay: 0.4s;
  background-color: #bd1550;
}
.ribbon {
  --f: 0.5em; /* control the folded part */

  position: absolute;
  top: 0;
  right: 0;
  line-height: 1.8;
  padding-inline: 1lh;
  padding-bottom: var(--f);
  border-image: conic-gradient(#0008 0 0) 51% / var(--f);
  clip-path: polygon(
    100% calc(100% - var(--f)),
    100% 100%,
    calc(100% - var(--f)) calc(100% - var(--f)),
    var(--f) calc(100% - var(--f)),
    0 100%,
    0 calc(100% - var(--f)),
    999px calc(100% - var(--f) - 999px),
    calc(100% - 999px) calc(100% - var(--f) - 999px)
  );
  transform: translate(calc((1 - cos(45deg)) * 100%), -100%) rotate(45deg);
  transform-origin: 0% 100%;
  /* the main color  */
}
