
                $minWidthPC: 769px;
$maxWidthMB: 768px;
                   @import "assets/main/scss/mixin.scss";
              
@import "./scss/sprite.scss";

#navigation {
  position: relative;
  height: 90px;
  pointer-events: auto;
  top: 20px;
  @include mobile {
    height: 102px;
    top: 45px;
  }
  &.active {
    .background {
      @include pc {
        opacity: 0.95;
      }
    }
  }
  .background {
    position: absolute;
    top: -20px;
    left: 5%;
    img {
      @include mobile {
        width: 690px;
      }
    }
    @include pc {
      opacity: 0.25;
      transition: opacity 0.35s linear;
    }
  }
  .content {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    box-sizing: border-box;
  }
  .content {
    padding: 0 7%;
    display: flex;
    align-items: center;
    gap: 20px;
    position: relative;
    @include mobile {
      top: -16px;
    }
    .logo {
      position: relative;
      &:after {
        content: "";
        border: 1px solid white;
        position: absolute;
        right: -20px;
        top: 0;
        height: 100%;
      }
    }
    .nav-pc {
      font-size: 38px;
      font-family: Arial;
      a {
        text-decoration: none;
        color: white;
      }
      ul {
        display: flex;
        li {
          opacity: 0;
          animation: spreadOut 0.5s ease-out forwards;
          list-style: none;
          cursor: pointer;
          a.active {
            color: #f7c30f;
          }
          &.special {
            span {
              color: #f7c30f;
            }
            &:before {
              content: "";
              @include sprite($v1);
              display: block;
              position: absolute;
              top: 0;
              left: -22px;
              transform: scale(1.2);
            }
          }
          &:after {
            content: "";
            width: 0;
            height: 3px;
            background: #f7c30f;
            position: absolute;
            left: 0;
            bottom: -10px;
            transition: 0.5s;
          }

          &:hover:after {
            width: 100%;
          }
        }

        // Tất cả li đều nhảy sang phải đều nhau
        @for $i from 1 through 10 {
          li:nth-child(#{$i}) {
            --x: #{$i * 75}px;
            animation-delay: #{1.3 + ($i - 1) * 0.2}s;
          }
        }
      }
      // Animation
      @keyframes spreadOut {
        to {
          opacity: 1;
          transform: translateX(var(--x));
        }
      }
    }
    .nav-mb {
      display: none;
      @include mobile {
        flex-direction: column;
        position: absolute;
        width: 90%;
        right: 40px;
        top: 96px;
        background-color: #282828;
        align-items: center;
        border-radius: 12px;
        display: flex;
        transform: translateY(-660px);
        transition: all 0.2s linear;
        z-index: -1;
        &.active {
          transform: translateY(0px);
        }
        ul {
          list-style: none;
          font-size: 42px;
          width: 40%;
          a {
            text-decoration: none;
            color: white;
            padding: 35px 0;
            display: block;
            text-align: center;
            width: 100%;
            border-bottom: 1px solid;
          }
        }
      }
    }
    .hireBtn {
      position: absolute;
      right: 7%;
      top: 20px;
      @include mobile {
        right: 47%;
        top: 10px;
      }
      .hire-btn {
        position: relative;
        transform: scale(1.5);
        transform-origin: top right;
        padding: 12px 24px;
        font-size: 16px;
        background: black; // Vàng giống footer
        color: #ffffff; // Trắng như chữ footer
        border: none;
        border-radius: 8px;
        cursor: pointer;
        display: flex;
        align-items: center;
        gap: 10px;
        transition: all 0.3s ease;
        text-decoration: none;
        .btn-text {
          font-weight: 600;
          font-family: system-ui, -apple-system, sans-serif;
        }

        .icon-container {
          position: relative;
          width: 24px;
          height: 24px;

          .icon {
            position: absolute;
            top: 0;
            left: 0;
            width: 24px;
            height: 24px;
            color: #ffffff;
            opacity: 0;
            visibility: hidden;
            transition: opacity 0.3s ease, visibility 0.3s ease;
          }

          .gmail-icon {
            opacity: 1;
            visibility: visible;
          }

          .wallet-icon {
            // chỉ hiện khi hover
          }
        }
        &.active {
          @include pc {
            background: #f7c30f;
          }
        }
        &:hover {
          @include pc {
            transform: scale(1.5) translateY(-2px);
            box-shadow: 0 5px 15px rgba(250, 204, 21, 0.4);
          }

          .gmail-icon {
            opacity: 0;
            visibility: hidden;
          }

          .wallet-icon {
            opacity: 1;
            visibility: visible;
          }
        }
      }
    }
    .btn-burger {
      @include sprite($burger);
      margin-left: auto;
      margin-right: 4px;
    }
  }
}
